.BussinessListingText{
   
    margin-left: 20%;
    margin-right: 20%;
    z-index: -1;
    /* padding-left: 40px;
    padding-right: 40px; */
    
}

.searchbar{
    display: flex;
    flex-direction: row;
    text-align: center;
    align-items: center;
    height: 10%;
    position: fixed;
    top: 80px;
    width: 60%;
    background-color: white;
    
   
   
}
.bussinessRegistaion{
   text-decoration: none;
}

.BussinessListing_btn{
    margin-left: 42%;
    
    display: flex;
    flex-direction: row;
    position: fixed;
    border: 2px solid #144273;
    border-radius: 8px;
    color: #144273;
    width: 18%;
    height: 5%;
    
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}   
.searchbars1{
   width: 16.5%;
    height: 5%;
    border-radius: 6px;
    padding-left: 2.5%;
    position: fixed;
    
}
.searchbars2{
    width: 17.5%;
    margin-left:17.5%;
    border-radius: 6px;
    position: fixed;
    padding-left: 1%;
    height: 5%;
    
}
.locationicon{
    position: fixed;
    margin-left: 0.6%;
    z-index: 2;
}
.searchbtn{
    position: fixed;
    margin-left: 32.5%;
    background-color: #C68643;
    border-radius: 4px;
    width: auto;
    
}
.btn_main1{
   text-transform: uppercase;
   background-color: white;
   font-family: "Poppins", sans-serif;
   font-weight: 600;
   font-style: normal;
   border: none;
   width: 35%;
   height: 100%;
}

.bussiness_btn{
    color: white;
    padding-left:5px;
    font-size: 83%;
    
}
.btn_main2{
    text-transform: uppercase;
    background-color: white;
    color: #144273;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-style: normal;
    border: none;
    width: 35%;
    height: 100%;
 }
 .profileicon{
   padding-right: 1%;
 }
 .btn_main3{
    text-transform: uppercase;
    background-color: white;
    color: #144273;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-style: normal;
    border: white 1px solid;
    border: none; 
    width: 100%;
    height: 100%;
    padding: auto;
    text-decoration: none;
 }
 .bussiness_banner{
    width: 100%;
    
 }
 .header_b1{
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-style: normal;
    font-size: 27px;
    color: #144273;
    text-align: start;
 }
 
 .serviceLogoBackground{
    width: 60px;
    height: 60px;
   
 }
 .view{
   width: 100%;
   background-color: #144273;
   color: white;
   font-weight: 600;
    font-style: normal;
   border: none;
 }
 .d-flex{
   display: flex;
   flex-direction: column;
 }
 #bussinessbanner .carousel-control-prev{
   display: none;
 }
 #bussinessbanner .carousel-control-next{
   display: none;
 }
 #bussinessbanner1 .carousel-control-prev{
   display: none;
   
 }
 #bussinessbanner1 .carousel-control-next {  
   width: 3%;
   z-index: none;
   display: none;
   
 }
 #bussinessbanner1 .carousel-control-next .carousel-control-next-icon{
   background-color: #144273;
      color: #144273;
      border-radius: 50%;
 }
 #bussinessbanner1 .carousel-item{
   width: 95%;
   transform: none;
 
   height: 450px;
   display: flex;
   flex-direction: row;
   position: relative;
   
 }
 .Bussinessbanner2{
   width: 100%;
   height: 100%;
   
 }
 .bannermap{
   width: 100%;
   height: 100%;
 }
.navagition{
   width: 100%;
   height: 100%;
   display: flex;
   flex-direction: row;
   justify-content: space-evenly;
   background-color: #E0EFFF;
   padding-top: 3%;
   padding-bottom: 3%;
}
.navagition1{
   height: 90%;
   width: 100%;
}

@media (max-width:768px) {
    .searchbar{
        display: flex;
        flex-direction: row;
        text-align: center;
        align-items: center;
       position: relative;
    }
   
    
}